






















































































































































































































































































import {Vue, Component} from "vue-property-decorator"
import { Select,  Page, Button, Option, Icon, Spin,DatePicker,RadioGroup,Radio,Checkbox} from 'view-design'
import { Tree } from 'iview'
import Input from '@manage/components/normalInput.vue';
import mixSelect from '@manage/components/mixSelect.vue'
import * as api from '@manage/api/propertyCharge/payWater'
import buildingTree from './components/buildingTreeLevel3.vue'
@Component({
    filters: {
        rounding (value) {
            return value.toFixed(2)
        }
    },
    components: {
        Icon,
        Input,
        Select,
        Option,
        Tree,
        Page,
        Button,
        Spin,
        DatePicker,
        mixSelect,
        RadioGroup,
        Radio,
        Checkbox,
        buildingTree
    }
})
export default class payWater extends Vue {
    payType:any=[]
    doType:any =[]
    code:any=[]
    dialogVisible:boolean=false
    payEndDate:string=''
    startTime:string=''
    endTime:string=''
    keyword:string=''
    endPaidMoney:string=''
    payTypeCode:string=''
    doTypeCode:string=''
    startPaidMoney:string=''
    single:boolean=false
    accountInfo:any={
        clientName:'',
        balance:'',
        lastPaidTime:'',
        latePaidMoney:'',
    }
    addPayWater:any={
        roomId:'',
        accountId:'',
        paidMoney:'',
        payTypeCode:'',
        documentCode:'',
        remark:'',
        documentId:'',
        // feeOperationTypeCode:'',
    }
    flag:boolean=true
    payWaterInfo:any=[]
    edit:boolean=false
    formHeight: number = 600

    curObject:any = {
        id:'',
        rdId:'',
    }
    renderContent(h, {root, node, data}) {
        return h('div', {
                attrs: {
                    title: data.title,
                },
                style: {
                    display: 'inline-flex',
                    alignItems: 'center',
                    width: 'calc(100% - 20px)',
                    height: '36px',
                    overflow: 'hidden',
                },
                class:{
                    treeActive:this.curObject.id === data.id,
                },
                on: {
                    click: (event) => {
                        if(data.level === 3){
                            this.curObject = data;
                            this.edit = false
                            this.getPayAccount(data.title)
                            setTimeout(()=>{
                                this.setPagesize();
                                this.resetInfo();
                            },500)
                        }
                        else{
                            this.gethouseData(data)
                        }
                    }
                }
            },
            [
                h('i', {
                    class: {
                        iconfont: true,
                        iconxiaoqu: data.level === 1,
                        iconlouyu: data.level === 2,
                    },
                    style: {
                        lineHeight: `1`,
                        marginLeft: '8px',
                        marginRight: '8px',
                        cursor: `pointer`,
                    },
                }),

                h('i', {
                    style: {
                        fontStyle: 'normal',
                        fontSize:  data.level === 1 ? '15px' : '14px',
                        fontWeight: data.level === 1 ? '600' : 'normal',
                        lineHeight: `16px`,
                        cursor: `pointer`,
                        overflow: `hidden`,
                        whiteSpace: `nowrap`,
                        textOverflow: `ellipsis`
                    },
                }, data.title),


            ]);
    };
    buildingTree: any = [{}]
    formHeight: number = 200;
    pages: any = {
        pageNum: 1,
        pageSize:10,
        totalElements: 0,
    };
    resetDialog:boolean = false;
    rePay:number = 0;
    loading:boolean = false;
    subFlag: boolean = true;
    mounted(): void {
        this.getSelection(`payType`,'支付方式')
        this.getSelection(`doType`,'预收款操作类型')
    }
    inited(curObj:any){
        this.curObject = curObj;
        this.getPayAccount(curObj.title);
        setTimeout(()=>{
            this.setPagesize();
            this.resetInfo();
        },200)
    }
    setPagesize(){
        let refs: any = this.$refs;
        let maxHeight = refs.rightMain.clientHeight;
        let searchline = refs.searchline.clientHeight + 20;
        let information = refs.information.clientHeight;
        let iconText = refs.iconText.clientHeight;
        //Page组件高度为53px，rightTitle:46,middleline:152
        this.formHeight = maxHeight - searchline - iconText - information - 53;
        this.pages.pageSize = Math.floor((this.formHeight - 49) / 48);
    }
    getPage(pageNum?: number) {
        if (pageNum) {
            this.pages.pageNum = pageNum;
        } else {
            this.pages.pageNum = 1;
        }
        this.getPayList()
    }
    created() {
        //this.gethouseData1()
    }
    editShow(){
        this.edit=true
    }
    async saveInfo(){
        if (this.subFlag) {
            this.subFlag = false;
            const confirmResult = await this.$confirm(
                '此操作将修改选中信息, 是否继续?',
                '操作提示',
                {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }
            ).catch(err => err)
            if (confirmResult !== 'confirm') {
                return this.$message.info('已取消修改')
            }
            this.accountInfo.roomId = this.curObject.id
            this.accountInfo.balance = Number(this.accountInfo.balance)
            try{
                await api.updatePayAccountInfo(this.accountInfo)
                this.getPayAccount()
                this.$message.success('修改成功')
            }catch (e) {
                this.$message.error('修改失败')
                this.getPayAccount()
            }
            this.edit=false
            this.subFlag = true;
        }

    }
    cancelInfo(){
        this.edit=false
    }
    async deleteSomeInfo() {
        let refs:any = this.$refs;
        if(refs.payWaterTable.selection.length===0){
            this.$message.info('请选择要删除的记录')
        }
        else {
            const confirmResult = await this.$confirm(
                '此操作将永久删除选中记录, 是否继续?',
                '操作提示',
                {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }
            ).catch(err => err)
            if (confirmResult !== 'confirm') {
                return this.$message.info('已取消删除')
            }
            let ids:any = refs.payWaterTable.selection.map(item => item.id)
            try {
                await api.deletePay(ids)
                this.resetInfo()
                this.getPayAccount()
                this.$message.success('删除成功')
            }catch (e) {
                this.$message.error('删除失败')
            }
        }
    }


    payDate(month){
        this.payEndDate=month

    }
    payableDate(daterange){
        this.startTime=daterange[0]
        this.endTime=daterange[1]

    }

    resetInfo(){
        let refs:any = this.$refs;
        this.payTypeCode=''
        this.payEndDate=''
        this.startTime=''
        this.endTime=''
        this.keyword=''
        this.startPaidMoney=''
        this.endPaidMoney=''
        refs.dateForm.handleClear()
        this.getPage()
    }
    async deleteInfo(id){
        const confirmResult = await this.$confirm(
            '此操作将永久删除选中的记录, 是否继续?',
            '操作提示',
            {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }
        ).catch(err => err)
        if (confirmResult !== 'confirm') {
            return this.$message.info('已取消删除')
        }
        var ids:any=[]
        ids=id.split('/t')
        try {
            await api.deletePay(ids)
            this.resetInfo()
            this.getPayAccount()
            this.$message.success('删除成功')
        }catch (e) {
            this.$message.error('删除失败')
        }
    }
    openDialog() {
        this.flag = true
        this.dialogVisible = true
        this.getCode()
    }
    closeDialog(){
        this.dialogVisible=false
        this.addPayWater={
            roomId:'',
            accountId:'',
            paidMoney:'',
            payTypeCode:'',
            documentCode:'',
            remark:'',
            documentId:'',
            // feeOperationTypeCode:'',
        }
    }
    //结构树
    async gethouseData1(){
        try{
            let { data } = await api.getTree(0)
            data.forEach(item =>{
                if(item.belongNum !== 0){
                    item.children = [{}];
                }
            })
            this.buildingTree = data
        }catch (e) {
            this.buildingTree = [];
            this.$message.error(`获取小区数据失败！`)
        }

    }
    treeExpand(data){
        this.gethouseData(data,true)
    }
    async gethouseData(obj: any,bl?:boolean) {
        let {data} = await api.getTree(obj.level, obj.id);

        if (data) {
            obj.children = data;
        }
        if(obj.level === 1){
            obj.children.forEach(item =>{
                item.rdId =  obj.id;
            })
            data.forEach(item =>{
                if(item.belongNum !== 0){
                    item.children = [{}];
                }
            })
        }
        else if(obj.level === 2){
            obj.children.forEach(item =>{
                item.rdId =  obj.rdId;
            })
        }
        if(!bl){
            obj.expand = !obj.expand;
        }
    }

    //新增账号
    async addPayAcount(){
        await api.addPayAcount({
            roomId:this.curObject.id
        })
        this.getPayAccount()
    }

    //查询房间
    async getPayList(){
        this.loading = true;
        try {
            let {data} = await api.getPayList({
                pageNum: this.pages.pageNum,
                pageSize: this.pages.pageSize,
                roomId: this.curObject.id,
                startTime: this.startTime,
                endTime: this.endTime,
                keyword: this.keyword,
                payTypeCode: this.payTypeCode,
                startPaidMoney: this.startPaidMoney,
                endPaidMoney: this.endPaidMoney,
                // feeOperationTypeCode:this.doTypeCode,
            })

            this.payWaterInfo=data.content
            this.pages.totalElements=data.totalElements
            this.loading = false;
        }catch (e) {
            this.loading = false;
        }

    }

    async getPayAccount(name?:string){
        try {
            let {data} = await api.getPayAccount(this.curObject.id)
            this.accountInfo = data
            if(!data){
                this.addPayAcount()
            }
            if(name){
                this.accountInfo.roomName = name;
            }
        }catch (e) {

        }
    }

    //新增预交
    async addPay(){
        this.addPayWater.roomId = this.curObject.id
        this.addPayWater.documentCode = this.code.code
        this.addPayWater.documentId = this.code.id
        this.addPayWater.accountId = this.accountInfo.id
        if(this.addPayWater.paidMoney===''){
            this.$message.error('请输入预交金额')
        }
        else if(this.addPayWater.payTypeCode===''){
            this.$message.error('请选择支付方式')
        }
        else if(this.flag){
            this.flag = false
            /*if(this.addPayWater.payTypeCode === '4'){
                this.addPayWater.feeOperationTypeCode = '2'
            }
            else{
                this.addPayWater.feeOperationTypeCode = '1'
            }*/
            try {
                await api.addPayMoney(this.addPayWater)
                this.$message.success('添加成功')
                this.dialogVisible=false
                this.resetInfo()
                this.getPayAccount()
            }catch (e) {
                this.$message.error('添加失败')
            }
            this.flag = true
        }
    }

    //可用单据
    async getCode(){
        let{data}= await api.getCode(this.curObject.rdId)
        this.code= data
    }


    async getSelection(Array: string, name: string) {
        let {data} = await api.getInfoByName(name);
        if(name === '预收款操作类型'){
            this[`${Array}`] = [{
                name: '全部类型',
                code: ''
            }].concat(data);
        }
        else{
            this[`${Array}`] = data;
        }

    }
    payTypeRename(code:string){
        let obj:any =  this.payType.find(item => item.code === code)
        return obj ? obj.name : `-`
    }
    doTypeRename(code:string){
        let obj:any =  this.doType.find(item => item.code === code)
        return obj ? obj.name : `-`
    }
}
